<template>
	<view class="chatRoom">
		<view class="notice-box">
			<text class="notice">当前有{{count}}人在线!</text>
		</view>
		<view class="content-box">
			<scroll-view :style="{height: viewHeight+'px'}" scroll-y :scroll-into-view="viewId" @scrolltolower="scroll">
				<view class="userMessage-box" v-for="item in chatRecordList" :key="item.id" :id="'item'+item.id">
					<view :class="item.ipAddress === ipAddress ? 'myHeader-box' : 'userHeader-box'">
						<image :class="item.ipAddress === ipAddress ? 'myAvatar' : 'userAvatar'" :src="item.avatar">
						</image>
						<view class="nickName-box">
							<text class="nickName">{{item.nickname}}</text>
							<text class="date">{{$dayjs(item.createTime).format('YYYY-MM-DD HH:mm:ss')}}</text>
						</view>
					</view>
					<view :class="item.ipAddress === ipAddress ? 'myContent-box' : 'userContent-box'">
						<!--文字表情消息内容-->
						<view v-if="item.type === 3" style="font-size: 30rpx;" v-html="item.content"></view>
						<!--音频消息-->
						<view v-if="item.type === 5">
							<audio :src="item.content"></audio>
						</view>
					</view>
					<view class="both"></view>
				</view>
			</scroll-view>
		</view>
		<view class="footer" ref="footer">
			<view class="content-wrap">
				<textarea class="content" v-model="message" maxlength="-1" />
			</view>
			<view class="btn-wrap">
				<button class="btn" :disabled="disable" :class="{'disabled': disable}" @click="handleSend()">发送</button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				isLogin: false,
				count: 1,
				viewHeight: null,
				footerHeight: 0,
				keyBoardHeight: 0,
				height: null,
				viewId: null, // scrollView标识id
				chatRecordList: [], // 历史聊天记录
				message: null,
				disable: true, // 禁用发送按钮
				ipAddress: "112.66.104.107",
				ipSource: null,
				defualtAvatar: "https://images.solargod.cn/solarBlog/config/a2ea0aab3456fd6655785f860034ff45.png",
				WebsocketMessage: {
					type: null,
					data: null
				},
			}
		},
		//获取屏幕可视区域高度（除去导航栏）
		onReady() {
			const self = this
			uni.getSystemInfo({
				success: (res) => {
					self.viewHeight = res.windowHeight - res.statusBarHeight - 55
				}
			})
		},

		watch: {
			//监听message，当他有值时发送按钮才可以点击
			message(newVal) {
				if (newVal.trim() != '') {
					this.disable = false
				} else {
					this.disable = true
				}
			},
			chatRecordList(newVal) {
				const len = newVal.length - 1
				this.viewId = 'item' + newVal[len].id
				// console.log('this.viewId', this.viewId)
			}
		},
		mounted() {
			//这里获取footer元素的高度，根据不同平台用的方式不同，对于uniapp的dom定位方法应该是通用的。特别注意，如果页面没有渲染出元素，可用this.$nextTick
			this.$nextTick(() => {
				// uniapp
				const footer = uni.createSelectorQuery().in(this);
				footer.select('.footer').boundingClientRect(data => {
					this.footerHeight = data.height
					this.height = this.viewHeight - this.footerHeight
					// console.log('this.footerHeight', this.footerHeight)
					// console.log('this.heightFoot', this.height)
				}).exec();
				// H5
				// this.footerHeight = this.$refs.footer.$el.offsetHeight
				// this.height = this.viewHeight - this.footerHeight
			})
		},
		onLoad() {
			this.connectSocketInit()
			// this.listenKeyboard()
		},
		onShow() {
			this.isLogin = uni.getStorageSync('isLogin')
		},
		onUnload() {
			this.destoryListenKeyboard()
		},
		methods: {
			scroll() {
				console.log('滑到底部')
			},
			listenKeyboard() {
				//监听键盘的高度变化，让sroll-view的高度随之变化
				uni.onKeyboardHeightChange(res => {
					let keyBoardHeight = res.height
					// console.log('keyBoardHeight', keyBoardHeight)
					if (this.keyBoardHeight == 0 && keyBoardHeight > 0) {
						this.keyBoardHeight = keyBoardHeight
					}
					if (keyBoardHeight > 0) {
						this.height = this.height - this.keyBoardHeight
					} else {
						this.height = this.height + this.keyBoardHeight
					}
					// console.log('this.heightKey', this.height)
				})
			},
			destoryListenKeyboard() {
				uni.offKeyboardHeightChange((res) => {
					console.log("closeKeyboardHeightChange...")
				})
			},
			connectSocketInit() {
				const self = this
				uni.connectSocket({
					// url: "ws://127.0.0.1:3300/websocket",
					url: "wss://websocket.solargod.cn",
					success(data) {
						console.log('WebSocket连接成功', data)
					}
				})

				uni.onSocketOpen(function(res) {
					console.log('WebSocket连接已打开！');
					setInterval(function() {
						var beatMessage = {
							type: 6,
							data: "ping"
						};
						uni.sendSocketMessage({
							data: JSON.stringify(beatMessage),
							success(res) {
								console.log('心跳重连成功', res)
							},
							fail(err) {
								uni.showToast({
									title: '重连失败!请重新启动!',
									icon:'none'
								})
							}
						})
					}, 30 * 1000)
				});

				uni.onSocketMessage(function(res) {
					const data = JSON.parse(res.data)
					switch (data.type) {
						case 1:
							self.count = data.data
							break;
						case 2:
							self.chatRecordList = data.data.chatRecordList
							self.ipAddress = data.data.ipAddress;
							self.ipSource = data.data.ipSource;
							break;
						case 3:
							// 文字消息
							self.chatRecordList.push(data.data);
							break;
					}

				});
			},
			isEmojiCharacter(substring) {
				for (var i = 0; i < substring.length; i++) {
					var hs = substring.charCodeAt(i);
					if (0xd800 <= hs && hs <= 0xdbff) {
						if (substring.length > 1) {
							var ls = substring.charCodeAt(i + 1);
							var uc = ((hs - 0xd800) * 0x400) + (ls - 0xdc00) + 0x10000;
							if (0x1d000 <= uc && uc <= 0x1f77f) {
								return true;
							}
						}
					} else if (substring.length > 1) {
						var ls = substring.charCodeAt(i + 1);
						if (ls == 0x20e3) {
							return true;
						}
					} else {
						if (0x2100 <= hs && hs <= 0x27ff) {
							return true;
						} else if (0x2B05 <= hs && hs <= 0x2b07) {
							return true;
						} else if (0x2934 <= hs && hs <= 0x2935) {
							return true;
						} else if (0x3297 <= hs && hs <= 0x3299) {
							return true;
						} else if (hs == 0xa9 || hs == 0xae || hs == 0x303d || hs == 0x3030 ||
							hs == 0x2b55 || hs == 0x2b1c || hs == 0x2b1b ||
							hs == 0x2b50) {
							return true;
			 		}
					}
				}
			},
			handleSend() {
				if (this.isLogin) {
					console.log('发送消息', this.message)
					if(this.isEmojiCharacter(this.message)) {
						uni.showToast({
							title: '发送失败,内容不要包含表情!',
							icon: 'none'
						})
					} else {
						var socketMsg = {
							nickname: uni.getStorageSync('nickname') || this.ipAddress,
							avatar: uni.getStorageSync('avatar') || this.defualtAvatar,
							content: this.message,
							userId: uni.getStorageSync('userId') || null,
							type: 3,
							ipAddress: this.ipAddress,
							ipSource: this.ipSource,
						};
						this.WebsocketMessage.type = 3;
						this.WebsocketMessage.data = socketMsg;
						uni.sendSocketMessage({
							data: JSON.stringify(this.WebsocketMessage),
							success(res) {
								console.log('发送成功', res)
							},
							fail(err) {
								console.log('发送失败', err)
							}
						})
						this.message = ''
					}
				} else {
					uni.navigateTo({
						url: "../mixLogin/mixLogin"
					})
				}
			},
			onShareAppMessage(){
				return {
					from:"menu"
				}
			},
			onShareTimeline(){},
		}
	}
</script>

<style lang="scss">
	.chatRoom {
		max-height: 100%;
		width: 100%;
		box-sizing: border-box;
		margin: 0;
		padding: 0;
	}
	
	.notice-box {
		position: relative;
		width: 100%;
		background-color: #FFFFFF;
	}
	
	.notice {
		color: #F08D49;
		display: flex;
		justify-content: center;
		align-content: center;
	}
	
	.userMessage-box {
		width: 100%;
		height: auto;
	}
	
	.myMessage-box {
		width: 100%;
		height: 100%;
	}
	
	.userHeader-box {
		width: 100%;
		height: 80rpx;
		display: flex;
		flex-direction: row;
	}
	
	.myHeader-box {
		width: 100%;
		height: 80rpx;
		display: flex;
		flex-direction: row-reverse;
	}
	
	.userAvatar {
		width: 80rpx;
		height: 80rpx;
		line-height: 80rpx;
		border-radius: 50%;
		margin-left: 10rpx;
		margin-top: 10rpx;
	}
	
	.myAvatar {
		width: 80rpx;
		height: 80rpx;
		line-height: 80rpx;
		border-radius: 50%;
		margin-right: 10rpx;
		margin-top: 10rpx;
	}
	
	.nickName {
		font-size: 20rpx;
		height: 80rpx;
		line-height: 80rpx;
		color: #007AFF;
		margin-left: 10rpx;
		font-weight: bold;
	}
	
	.date {
		font-size: 20rpx;
		height: 80rpx;
		line-height: 80rpx;
		margin-left: 10rpx;
	}
	
	.userContent-box {
		position: relative;
		background-color: #FFFFFF;
		padding: 10px;
		border-radius: 5px 20px 20px 20px;
		width: fit-content;
		color: black;
		margin-left: 90rpx;
	}
	
	.myContent-box {
		position: relative;
		border-radius: 20px 5px 20px 20px;
		width: fit-content;
		padding: 10px;
		background: #12b7f5;
		color: #fff;
		float: right;
		margin-right: 90rpx;
	}
	
	.both {
		clear: both;
	}
	
	.audio-box {
		width: 50rpx;
		height: 50rpx;
	}
	
	.audio {
		width: 50rpx;
		height: 50rpx;
		line-height: 50rpx;
	}
	
	.footer {
		margin-top: 39rpx;
		width: 100%;
		height: 75rpx;
		background-color: #E9EDF4;
		display: flex;
		position: sticky;
		bottom: 0;
		justify-content: space-between;
		align-items: center;
	}
	
	.footer .content-wrap {
		width: 78vw;
		margin-left: 2vw;
	}
	
	.footer .content {
		width: 100%;
		box-sizing: border-box;
		margin: 14rpx 0;
		background-color: #FFFFFF;
		border-radius: 30rpx;
		padding: 6rpx;
		caret-color: #01B4FE;
		height: 65rpx;
	}
	
	.footer .btn-wrap {
		width: 18vw;
		margin-right: 2vw;
	}
	
	.footer .btn {
		width: 16%;
		height: 50rpx;
		text-align: center;
		font-size: 20rpx;
		margin-left: 2%;
		background-color: #01B4FE;
		color: #FFFFFF;
		position: fixed;
		border: 0;
		outline: none;
		bottom: 3.2vh;
	}
	
	.footer .btn-wrap .disabled {
		background-color: #aae8f5;
	}
	
	/deep/ .uni-textarea-wrapper {
		min-height: 70rpx;
	}
</style>
